.products-container {
    @apply mx-auto p-4;
    max-width: 100%;
}

.title {
    @apply text-2xl font-bold mb-4;
}

.products-grid{
    @apply grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 pb-16;
}

.footer{
    @apply fixed bottom-0 left-0 flex justify-center w-full;
}

.button-green {
    @apply bg-green-500 text-white py-2 hover:bg-green-600 w-full text-center;
}